/* --------- APP HEADER --------- */

.App-logo {
  height       : 50px ;
  width        : 50px ;
  padding-top  : 5px  ;
  padding-left : 20px ;
  padding-right: 10px ;
  float        : left ;

}


.PersonalPageHeader {
  background-color: black               ;
  opacity         : 0.95                ;
  height          : 65px                ;
  width           : 100%                ;
  color           : black               ;
  float           : center              ;
  text-align      : center              ;
  -webkit-box-shadow      : 0px 0.51px 3px grey ;
          box-shadow      : 0px 0.51px 3px grey ;
  position        : fixed               ;
  z-index         : 5                   ;
  overflow        : hidden              ;
}

.PersonalPageHeader ul {
  list-style-type: none   ;
  margin         : 0      ;
  padding        : 0      ;
  overflow       : hidden ;
}

.PersonalPageHeader li {
  float        : left;
}




.PersonalPageHeader button{
  float: left ; 
  background-color   : inherit           ;
  color              : #D5D5D5           ;
  font-size          : 16px              ;
  height             : 65px              ;
  width              : 150px             ;
  border             : none              ;
  padding-right      : 12px              ;
  padding-left       : 12px              ;
  -webkit-user-select: none              ;
  -moz-user-select        : none              ;
   -ms-user-select        : none              ;
       user-select        : none              ;

}

.PersonalPageHeader button:hover{
  background-color: #5A5A5A ;
  -webkit-transition      : 0.6s    ;
  -o-transition      : 0.6s    ;
  transition      : 0.6s    ;
}

.PersonalPageHeader button:focus{
    outline: none ;
}






/* =====================  GENERAL  ===================== */


.Background img {
    position  : fixed  ;
    right     : 0      ;
    top       : 0      ;
    width     : 100%   ;
    height    : auto   ;
    min-width : 1400px ;
    z-index   : -5     ;
}


.teamName{
  position  : absolute ;
  color     : white    ;
  top       : 0px      ;  
  right     : 5%       ; 
  width     : 33%      ;
  font-size : 24px     ;
  text-align: right    ;
  z-index   : 0        ;
  font-family: 'Great Vibes', cursive;
}


/* =====================  INTRO  ===================== */


.Intro .react-select__container{
  position: absolute;
  top: 50%;
  left:30%;
  width:40%;
}


.Intro .react-select__control{
  background-color: rgba(7,7,7,0.6);
  border-color:#F0F0F0;
  border-radius:1px;
  border-width:1px;
}


.Intro .react-select__menu{
  background-color: rgba(7,7,7,0.7);
  border-color:#F0F0F0;
  border-radius:1px;
  border-width:1px;
  max-height: 400%;
}

.Intro .react-select__menu-list{
  color:white;
  padding-top: 0;
  padding-bottom: 0;
}

.Intro .react-select__menu-list .react-select__option--isFocused{
  background: rgba(80,80,80,0.7); 
  color:white;
}



.Intro .react-select__input input{
  color: white;
}

.Intro .react-select__single-value{
  color:white;
}





.Intro .ConvocTitle{
  position  : absolute ;
  color     : white    ;
  top       : 0%       ;  
  left      : 0%       ;
  width     :100%      ; 
  text-align: center   ;
  font-size : 74px     ;
  text-anchor: start   ;
  font-family: 'Great Vibes', cursive;
}


.Intro .HPLogo img{
    position  : fixed  ;
    top       : 28%    ;
    left      : 40%    ;
    width     : 20%    ;
    height    : auto   ;
    min-width : 100px  ;
    opacity   : 0.85   ;
}

/* =====================  HOME  ===================== */

.Home .ProfilePicture{
  position : absolute ;
  width    : 200px    ;
  height   : auto     ;
  top      : 18%      ;
  right    : 12%       ;
  border   : 1px solid #021a40;
  border-radius: 6px;
  -webkit-box-shadow: 0px 0px 10px 1px rgba(0, 0, 0, 0.8), 0 0 20px 2px rgba(0, 0, 0, 0.1);
          box-shadow: 0px 0px 10px 1px rgba(0, 0, 0, 0.8), 0 0 20px 2px rgba(0, 0, 0, 0.1);

}

.Home .textTitle{
  position  : absolute ;
  color     : white    ;
  top       : 110px     ;  
  left      : 10%      ; 
  font-size : 40px     ;
  text-align: left     ;
  z-index   : 0        ;
}

.Home .textExplication{
  position  : absolute ;
  color     : white    ;
  top       : 210px    ;  
  left      : 10%      ;
  width     : 50%    ;  
  font-size : 20px     ;
  text-align: left    ;
  z-index   : 0        ;
}


